import "./styles.css";
const api = `https://randomuser.me/api`;
const addUser = document.getElementById("user-btn");
const mainApp = document.getElementById("app");

addUser.addEventListener("click", async () => {
  const userDate = await fetch(api, {
    method: "GET"
  });
  const userJson = await userDate.json();
  //console.log( userJson.results[0]);
  const user = userJson.results[0];
  const userEl = document.createElement("div");
  userEl.innerHTML = `<div>
   ${user.name.first} ${user.name.title} ${user.name.last}
  </div>`;
  mainApp.appendChild(userEl);
});
